@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #FFF;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app
{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    
    height: auto;
    width: 100%;
}
.loading
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    height: 100vh;
    width: 100vw;
}

.loading img {
    width: 100%;
}
:root {
    --hex-green: #57c754;
    --hex-red: #f27a41;
    --hex-blue: #0fadd4;
    --hex-yellow: #e0e016;
    --hex-white: #77ccd1;
    --hex-purple: #a77ecf;
    --hex-brown: #dba560;
    --hex-pink: #e37197;
    --hex-gray: #829299;
    --hex-black: #0d3140;
}

.green {background: #57c754;background: var(--hex-green);}
.red {background: #f27a41;background: var(--hex-red);}
.blue {background: #0fadd4;background: var(--hex-blue);}
.yellow {background: #e0e016;background: var(--hex-yellow);}
.white {background: #77ccd1;background: var(--hex-white);}
.purple {background: #a77ecf;background: var(--hex-purple);}
.brown {background: #dba560;background: var(--hex-brown);}
.pink {background: #e37197;background: var(--hex-pink);}
.gray {background: #829299;background: var(--hex-gray);}
.black {background: #0d3140;background: var(--hex-black);}

.color-green {color: #57c754;color: var(--hex-green);}
.color-red {color: #f27a41;color: var(--hex-red);}
.color-blue {color: #0fadd4;color: var(--hex-blue);}
.color-yellow {color: #e0e016;color: var(--hex-yellow);}
.color-white {color: #77ccd1;color: var(--hex-white);}
.color-purple {color: #a77ecf;color: var(--hex-purple);}
.color-brown {color: #dba560;color: var(--hex-brown);}
.color-pink {color: #e37197;color: var(--hex-pink);}
.color-gray {color: #829299;color: var(--hex-gray);}
.color-black {color: #0d3140;color: var(--hex-black);}


.container-list
{    
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto;
    
    font-family: 'Fjalla One', sans-serif;

    grid-gap: 5px;

    gap: 5px;
}
.container-card
{
    position: relative;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    border-radius: 5px;
    height: calc(100vh / 4);
}
.feature
{
    position: absolute;
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    
    color: #FFF;

    font-size: 1.2rem;
    height: 100%;
    width: 100%;
}
.container-card .number
{
    padding: 10px;
}
.container-card img {
    width: 100%;
}


@media (min-width: 768px) {
    .container-list
    {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: 1fr;
        
        margin: 0 calc(100vw / 5);
    }
    .container-card
    {
        height: calc(100vh / 4);
    }
}
.loading
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    height: 100vh;
    width: 100vw;
}
.loading #top
{
    fill: #F00;
}
.loading #bottom,
.loading #middle
{
    fill: #CCD;
}
.loading #middle
{
    -webkit-animation: blink 600ms infinite;
            animation: blink 600ms infinite;
}
@-webkit-keyframes blink
{
    from { fill: red; }
}
@keyframes blink
{
    from { fill: red; }
}
.loading svg
{
    -webkit-animation: tilt 1000ms alternate infinite;
            animation: tilt 1000ms alternate infinite;
}
@-webkit-keyframes tilt
{
    0% { -webkit-transform: rotate(20deg); transform: rotate(20deg); }
    50% { -webkit-transform: rotate(-20deg); transform: rotate(-20deg); }
    100% { -webkit-transform: rotate(s20deg); transform: rotate(s20deg); }
}
@keyframes tilt
{
    0% { -webkit-transform: rotate(20deg); transform: rotate(20deg); }
    50% { -webkit-transform: rotate(-20deg); transform: rotate(-20deg); }
    100% { -webkit-transform: rotate(s20deg); transform: rotate(s20deg); }
}
.pokemon-container
{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
    "c1 c1 c1 c1"
    "c2 c2 c2 c2"
    "c3 c3 c3 c3"
    "c4 c5 c5 c5"
    "c6 c6 c6 c6"
    "c7 c7 c7 c7";
    
    color: white;

    font-weight: 100;
    font-family: 'Fjalla One', sans-serif;
}
:is(.c-1, .c-2, .c-3, .c-4, .c-5, .c-6, .c-7) img
{
    width: 100%;
}
:is(.c-1, .c-2, .c-3, .c-4, .c-5, .c-6, .c-7)
{
    padding: 10px;
    font-weight: 100;
}
.c-1 {grid-area: c1; opacity: 0.95;}
.c-2 {grid-area: c2; opacity: 0.9;}
.c-3 {grid-area: c3; opacity: 0.85;}
.c-4 {grid-area: c4; opacity: 0.8;}
.c-5 {grid-area: c5; opacity: 0.75;}
.c-6 {grid-area: c6; opacity: 0.7;}
.c-7 {grid-area: c7; opacity: 0.95;}
.c-1
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.c-1 .number
{
    -webkit-align-self: flex-start;
            align-self: flex-start;
}
.c-1 .name
{
    font-size: 1.8rem;
}
.c-2
{
    padding: 0 3rem;
}
.c-4
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.c-4 div
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.c-4 img
{
    width: 2.5rem;
}
.c-4 .type
{
    font-size: 0.9em;
}
.c-5
{
    display: grid;
}
.c-5 div
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-self: center;
    color: #111;
}
.c-5 div:nth-child(even)
{
    color: #FFF;
}


@media (min-width: 768px) {
    .pokemon-container
    {
        display: grid;
        grid-template-areas:
        "c1 c1 c1 c1"
        "c2 c2 c3 c3"
        "c2 c2 c5 c4"
        "c2 c2 c6 c6"
        "c7 c7 c6 c6"
        ;
        margin: 0 5rem;
    }
}
.carousel-container
{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;

    width: 100%;
}
.carousel-container button
{
    border: none;
    outline: none;
    cursor: pointer;
    background: none;
}
.carousel
{
    display: -webkit-flex;
    display: flex;
    width: 100%;
}
.slide
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    text-align: center;
}
.slide img
{
    height: auto;
    width: 100%;
}
:is(
    .slide:first-child,
    .slide:last-child
    )
{
    opacity: .7;

    height: auto;
    width: 50%;
}
:is(
    .slide:first-child,
    .slide:last-child
    ) span
{
    display: none;
}
.zoom
{
    -webkit-animation: left 600ms forwards;
            animation: left 600ms forwards;
}
@-webkit-keyframes left {
    0%{
        -webkit-transform: scaleY(50%) scaleX(50%);
                transform: scaleY(50%) scaleX(50%);
        color: transparent;
    }
    99%{
        -webkit-transform: scaleY(100%) scaleX(100%);
                transform: scaleY(100%) scaleX(100%);
        color: transparent;
    }
    100%{
        color: white;
    }    
}
@keyframes left {
    0%{
        -webkit-transform: scaleY(50%) scaleX(50%);
                transform: scaleY(50%) scaleX(50%);
        color: transparent;
    }
    99%{
        -webkit-transform: scaleY(100%) scaleX(100%);
                transform: scaleY(100%) scaleX(100%);
        color: transparent;
    }
    100%{
        color: white;
    }    
}
